.container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 450px;
  color: white;
}

.board {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 80px 80px 80px;
      grid-template-columns: 80px 80px 80px;
  width: 240px;
  height: 240px;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  margin-top: 20px;
}

.tile {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  width: 75px;
  height: 75px;
  font-size: 44px;
  background-color: #27282b;
  color: #16bdca;
  border-radius: 20px;
  cursor: pointer;
  outline: 0;
  border: 0;
  -webkit-transition: 200ms;
  transition: 200ms;
}

.tile:hover {
  background-color: #16bdca;
  color: #27282b;
}

.active {
  color: #194a51;
  background-color: #1a1a1e;
  border: #194a51 2px solid;
}

.active:hover {
  background-color: #1a1a1e;
  color: #194a51;
}

