$cyan: #16bdca;
$dk-cyan: #179ca8;
$turqoise: #194a51;
$dk-gray: #27282b;
$black: #1a1a1e;


.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 450px;
    color: white;
}

.board {
    display: grid;
    grid-template-columns: 80px 80px 80px;
    width: 240px;
    height: 240px;
    justify-content: space-between;
    margin-top: 20px;
}

.tile {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 75px;
    height: 75px;
    font-size: 44px;
    background-color: #27282b;
    color: $cyan;
    border-radius: 20px;
    cursor: pointer;
    outline: 0;
    border: 0;
    transition: 200ms;
    &:hover {
        background-color: $cyan;
        color: $dk-gray;
    }
}

.active {
    color: $turqoise;
    background-color: $black;
    border: $turqoise 2px solid;
    &:hover {
        background-color: $black;
        color: $turqoise;
    }
}



footer {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 100px;
    margin-top: 15px;
        .steps {
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 22px;
            color: $turqoise;
            width: 32px;
            height: 32px;
            border: solid 2px;
            border-radius: 9px;
        }
        .restart {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 75px;
            height: 38px;
            font-size: 13px;
            color: $black;
            background-color: $turqoise;
            border-radius: 9px;
            cursor: pointer;
        }
        .active-restart {
            background-color: $dk-cyan;
            &:hover {
                background-color: $cyan;
            }

        }
}